.advantage {
    z-index: 0;
    position: relative;
    padding-top: 227px;
    padding-bottom: 341px;
    ;
    background-color: var(--black);

    & .container {
        &:hover .advantage__title::after {
            width: 120%;
        }
    }

    &__title {
        position: relative;
        font-family: 'Be Vietnam Pro';
        font-style: normal;
        font-weight: 700;
        font-size: 35px;
        line-height: 51px;
        color: var(--d);
        border: 1px solid #000000;

        &::after {
            position: absolute;
            content: "";
            width: 0;
            height: 3px;
            background-color: var(--d);
            bottom: -10px;
            left: 50%;
            transition: 0.3s;
            transform: translateX(-50%);
        }


    }

    &__desc {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 800;
        font-size: 35px;
        line-height: 52px;
        text-align: center;
        color: var(--white);
        max-width: 573px;
        margin: 45px 0 50px;

    }

    &__cards {
        row-gap: 33px;
        column-gap: 50px;
    }

    & .card {
        background: var(--white);
        box-shadow: 0px 13px 19px rgba(0, 0, 0, 0.07);
        border-radius: 30px;
        width: 334px;
        height: 334px;

        &__title {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            text-align: center;
            letter-spacing: 0.1px;
            color: var(--black);
        }

        &__desc {
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            letter-spacing: 0.2px;
            color: #737373;
            margin: 16px 0 10px;
        }
    }

    &__map {
        z-index: -1;
        position: absolute;
        bottom: 149px;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 100%;
    }
}


@media (max-width: 1050px) {
    .advantage {
        &__cards {
            grid-template-columns: 1fr;
        }

        &__map {
            bottom: 0;
            top: 0;
            left: 0;
            right: 0;
            margin: auto;
        }
    }
}


@media (max-width: 500px) {
    .advantage {
        & .container {
            text-align: center;
        }

        &__title {
            font-size: 26px;
        }

        &__desc {
            font-size: 24px;
        }
    }
}


@media (max-width: 500px) {
    .advantage {
        & .card {
            padding: 5px;
            width: 250px;
            height: 250px;
            &__title {
                font-size: 14px;
            }
            &__desc {
                font-size: 12px;
            }
        }
    }
}