.abouthome {
    padding-top: 192px;
    background-color: var(--black);

    & .container {
        &:hover .abouthome__title::before {
            height: 120%;
        }
    }

    &__title {
        font-family: 'Nunito';
        font-style: normal;
        font-weight: 600;
        font-size: 35px;
        line-height: 48px;
        color: var(--d);
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        position: relative;
        margin-left: 13px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: -13px;
            width: 2px;
            height: 0%;
            background-color: var(--d);
            transform: rotate(90);
            transition: 0.3s;
        }
    }

    &__desc {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 50px;
        letter-spacing: 0.05em;
        color: var(--white);
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    &__more {
        background: var(--d);
        border-radius: 100px;
        width: 195px;
        height: 46px;
        color: var(--white);
        font-family: 'Nunito';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 22px;
        color: #FFFFFF;
        border: 1px solid #846D45;
        margin-top: 30px;
        cursor: pointer;

        &:hover {
            background: var(--white);
            border: 1px solid var(--white);
            color: #846D45;
        }
    }
}


@media (max-width: 900px) {
    .abouthome {
        &__title {
            margin-bottom: 20px;

            &::before {
                display: none;
            }

            &::after {
                content: "";
                position: absolute;
                bottom: 2px;
                left: 0;
                right: 0;
                margin: 0 auto;
                width: 0;
                height: 3px;
                background-color: var(--d);
                transition: 0.3s;
            }
        }

        & .container {
            grid-template-columns: 1fr;
            text-align: center;

        }

        &:hover .abouthome__title::after {
            width: 80%;
        }
    }
}

@media (max-width: 600px) {
    .abouthome {
      
        &:hover .abouthome__title::after {
            width: 100%;
        }
    }
}



@media (max-width: 500px) {
    .abouthome {
      &__title {
        font-size: 30px;
      }
       &__desc {
        font-size: 15px;
       }
    }
}